@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&family=Roboto+Slab:wght@300;700&display=swap);
:root {
  --font-montserrat: "Montserrat", sans-serif;
  --font-roboto: "Roboto Slab", sans-serif;
}

/* Layout & Content*/
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
table,
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  padding: 0;
  margin: 0;
}

html {
  height: 100vh;
  font-size: 1px; /*for using REM units*/
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  font-size: 16rem;
  margin: 0;
  line-height: 1.5;
  font-family: "Montserrat", sans-serif;
  font-family: var(--font-montserrat);
}

template,
[hidden] {
  display: none;
}

textarea {
  overflow: auto;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

img {
  border-style: none;
  max-width: 100%;
  display: block;
}

/* Typography */

cite {
  font-style: normal;
}

h1 {
  font-size: 28rem;
}

h2 {
  font-size: 24rem;
}

h3 {
  font-size: 20rem;
}

h4 {
  font-size: 16rem;
}

h5 {
  font-size: 15rem;
}

h6 {
  font-size: 14rem;
}

pre {
  font-family: monospace;
  font-size: 1em;
}

a {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
  background-color: transparent;
}

abbr {
  border-bottom: none;
  -webkit-text-decoration: underline dotted;
          text-decoration: underline dotted;
}

b,
strong {
  font-weight: bolder;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25rem;
}

sup {
  top: -0.5rem;
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

details {
  display: block;
}

summary {
  display: list-item;
}

/* Forms, Input and Buttons */

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

button,
input {
  overflow: visible;
}

input {
  background-color: transparent;
  color: inherit;
  padding: 0;
  margin: 1px;
  border: 1px solid gray;
}

input[type="submit"],
input[type="button"],
input[type="reset"] {
  cursor: pointer;
}

input:disabled {
  border: 1px solid gray;
  cursor: not-allowed;
}

input:focus,
textarea:focus,
select:focus {
  outline: none;
}

button {
  border-width: 0;
  padding: 0;
  cursor: pointer;
}

button,
select {
  text-transform: none;
}

button:disabled {
  cursor: not-allowed;
}

fieldset {
  border-width: 0;
  padding: 0;
  margin: 0;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

progress {
  vertical-align: baseline;
}

input,
button,
textarea,
select {
  font: inherit;
}

/* Remove _all_ animations and transitions for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  * {
    -webkit-animation-play-state: paused !important;
            animation-play-state: paused !important;
    transition: none !important;
    scroll-behavior: auto !important;
  }
}

.rmdb-header {
  width: 100%;
  height: auto;
  background: #1c1c1c;
  padding: 0 20px;
  box-sizing: border-box;
}

.rmdb-header-content {
  max-width: 1280px;
  min-height: 40px;
  height: auto;
  padding: 10px 0px;
  margin: 0 auto;
  box-sizing: border-box;
  overflow: hidden;
}

.rmdb-logo {
  display: flex;
  align-items: center;
}

.rmdb-logo span {
  color: white;
  font-family: var(--font-roboto);
  font-size: xx-large;
  font-weight: bold;
  font-style: italic;
}

@media screen and (max-width: 500px) {
  .rmdb-header-content {
    max-width: 1280px;
    min-height: 0px;
  }

  .rmdb-logo {
    width: 150px;
    margin-top: 5px;
  }
}

.rmdb-home {
  margin-bottom: 100px;
}

.rmdb-home-grid {
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 20px;
}

.rmdb-grid-header {
  display: flex;
  justify-content: space-between;
}
.rmdb-heroimage {
  background-size: 100%, cover !important;
  background-position: center, center !important;
  width: 100%;
  height: 100%;
  height: 600px;
  position: relative;
  -webkit-animation: animateHeroimage 1s;
          animation: animateHeroimage 1s;
}

@-webkit-keyframes animateHeroimage {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes animateHeroimage {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.rmdb-heroimage-content {
  min-width: 500px;
  padding: 20px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  color: #fff;
}

.rmdb-heroimage-text {
  z-index: 100;
  max-width: 700px;
  margin-right: 20px;
  min-height: 100px;
  background: rgba(0, 0, 0, 0);
}

.rmdb-heroimage-text h1 {
  font-family: var(--font-roboto);
  font-size: 48px;
}

.rmdb-heroimage-text>a:hover {
  color: cadetblue;
}

.rmdb-heroimage-text p {
  font-family: var(--font-roboto);
  font-size: 22px;
  line-height: 26px;
}

@media screen and (max-width: 720px) {
  .rmdb-heroimage-text {
    max-width: 100%;
  }

  .rmdb-heroimage-text h1 {
    font-size: 38px;
  }

  .rmdb-heroimage-text p {
    font-size: 16px;
    line-height: 20px;
  }
}

.rmdb-searchbar {
  min-width: 500px;
    width: 100%;
    height: 105px;
    position: relative;
    padding: 25px 20px 0px 20px;
    box-sizing: border-box;
    color: #fff;
  }
  
  .rmdb-searchbar-content {
    max-width: 1280px;
    width: 100%;
    height: 55px;
    background: white;
    margin: 0 auto;
    border-radius: 40px;
    position: relative;
    color: #353535;
  }
  
  .rmdb-icon-search {
    position: absolute;
    left: 5px;
    top: 5px;
  }
  
  .rmdb-searchbar-input {
    font-family: 'Abel', sans-serif;
    font-size: 38px;
    position: absolute;
    left: 70px;
    top: 7px;
    border: 0;
    background: transparent;
    height: 40px;
  }
  
  .rmdb-searchbar-input:focus {
    outline: none;
  }
  
  @media screen and (max-width: 720px) {
    .rmdb-searchbar-input {
      font-size: 28px;
    }
  }
.rmdb-grid h1 {
    font-family: 'Abel', sans-serif;
    font-size:42px;
  }
  
  .rmdb-grid-content {
    display: grid;
    grid-template-columns: auto auto auto auto;
  }
  
  .rmdb-grid-element {
    margin: 0 40px 40px 0;
    background: #353535;
    max-height: 430px;
    -webkit-animation: animateGrid 0.5s;
            animation: animateGrid 0.5s;
    overflow: hidden;
  }
  
  @-webkit-keyframes animateGrid {
    from {
        opacity:0;
    }
    to {
        opacity:1;
    }
  }
  
  @keyframes animateGrid {
    from {
        opacity:0;
    }
    to {
        opacity:1;
    }
  }
  
  /* For a 4-column grid */
  .rmdb-grid-element:nth-child(4n+4) {
    margin-right: 0;
  }
  
  @media screen and (max-width: 720px) {
    .rmdb-grid-content {
      grid-template-columns: auto auto;
    }
  
    .rmdb-grid-element:nth-child(2n+2) {
      margin-right: 0;
    }
  
    .rmdb-grid h1 {
      font-size:22px;
    }
  }
.rmdb-moviethumb img {
  width: 500px;
  height: auto;
  max-width: 100%;
  max-height: 100%;
  transition: all 0.3s;
  box-sizing: border-box;
}

.clickable {
  cursor: pointer;
}

.clickable:hover {
  opacity: 0.8;
}

.rmdb-loadmorebtn {
  background: cadetblue;
  width: 100%;
  min-height: 50px;
  text-align: center;
  color: #fff;
  cursor: pointer;
  transition: all 0.3s;
}

.rmdb-loadmorebtn p {
  font-family: "Abel", sans-serif;
  font-size: 42px;
  padding: 20px;
}

.rmdb-loadmorebtn:hover {
  opacity: 0.8;
}

.loader {
  border: 5px solid #f3f3f3; /* Light grey */
  border-top: 5px solid cadetblue; /* Blue */
  border-radius: 50%;
  width: 50px;
  height: 50px;
  -webkit-animation: spin 0.8s linear infinite;
          animation: spin 0.8s linear infinite;
  margin: 20px auto;
}

@-webkit-keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.star-rating {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  padding: 5px;
  border: solid 1px gray;
  border-radius: 10px;
}

.star {
  cursor: pointer;
  width: 2em;
  height: 2em;
  background-color: grey;
  -webkit-clip-path: polygon(
    50% 0%,
    63% 38%,
    100% 38%,
    69% 59%,
    82% 100%,
    50% 75%,
    18% 100%,
    31% 59%,
    0% 38%,
    37% 38%
  );
  clip-path: polygon(
    50% 0%,
    63% 38%,
    100% 38%,
    69% 59%,
    82% 100%,
    50% 75%,
    18% 100%,
    31% 59%,
    0% 38%,
    37% 38%
  );
}

.star:hover {
  width: 3em;
  height: 3em;
}

.star.selected {
  background-color: red;
}

.rmdb-navigation {
  width: 100%;
  height: 50px;
  background: #353535;
  color: #fff;
  position: relative;
  padding: 20px;
  box-sizing: border-box;
  margin: 0;
  padding-top: 10px;
}

.rmdb-navigation-content {
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 20px;
}

.rmdb-navigation-content p {
  font-family: var(--font-roboto);
  font-size: 22px;
  float: left;
  color: #fff;
  padding-right: 10px;
  text-decoration: none;
  margin: 0;
}

.rmdb-movieinfo {
  background-size: cover !important;
  background-position: center !important;
  width: 100%;
  height: 600px;
  padding: 40px 20px;
  box-sizing: border-box;
  -webkit-animation: animateMovieinfo 1s;
          animation: animateMovieinfo 1s;
}

@-webkit-keyframes animateMovieinfo {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes animateMovieinfo {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.rmdb-movieinfo-content {
  max-width: 1280px;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  background: rgb(0, 0, 0, 0.9);
  position: relative;
}

.rmdb-movieinfo-thumb {
  width: 350px;
  height: 100%;
  overflow: hidden;
  position: absolute;
  left: 0px;
}

.rmdb-movieinfo-text {
  height: 100%;
  width: auto;
  color: #fff;
  padding: 40px;
  overflow: hidden;
  box-sizing: border-box;
  position: absolute;
  left: 360px;
}

.rmdb-movieinfo-text h1 {
  font-family: var(--font-roboto);
  font-size: 48px;
  margin: 0;
}

.rmdb-movieinfo-text h3 {
  font-size: 16px;
  line-height: 0;
  margin-top: 30px;
  margin-bottom: 10px;
}

.rmdb-movieinfo-text p {
  font-family: var(--font-roboto);
  font-size: 18px;
  line-height: 26px;
}

.rmdb-rating {
  width: 250px;
  height: 20px;
  margin-top: 20px;
  position: relative;
}

.rmdb-score {
  position: absolute;
  margin: 0;
  right: 0px;
  top: -3px;
}

.rmdb-director {
  margin: 10px 0;
}

.homepage-link {
  position: absolute;
  bottom: 40px;
  right: 40px;
  color: #fff;
}

meter::-webkit-meter-bar {
  background: #fff;
  width: 200px;
}
meter::-webkit-meter-optimum-value {
  background: linear-gradient(to bottom, #16d47b);
}
meter::-webkit-meter-suboptimum-value {
  background: linear-gradient(to bottom, #fbb450);
}
meter::-webkit-meter-even-less-good-value {
  background: linear-gradient(to bottom, #ee5f5b);
}

.rmdb-movieinfobar {
  width: 100%;
  height: 105px;
  background: #1c1c1c;
  position: relative;
  padding: 25px 20px 0px 20px;
  box-sizing: border-box;
  font-family: "Abel", sans-serif;
  font-size: 22px;
}

.rmdb-movieinfobar-content {
  max-width: 1280px;
  width: 100%;
  margin: 0 auto;
  color: #fff;
}

.rmdb-movieinfobar-content-col {
  float: left;
  width: 33.33%;
  box-sizing: border-box;
  padding: 10px 20px 0 0;
}

.rmdb-movieinfobar-info {
  padding: 5px 0 0 10px;
  float: left;
}

.icon-time,
.icon-revenue,
.icon-budget {
  float: left;
  margin-top: -4px;
}

.rmdb-movie {
  margin-bottom: 100px;
}

.rmdb-movie-grid {
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 20px;
}

.rmdb-movieinfo-text a:hover,
.rmdb-navigation-content a:hover {
  transform: scale(1.1);
}
